import { Builder } from './Builder'

import { createReadStream, type ReadStream } from 'fs'
import FORMDATA from 'form-data'

export function FormData(name: string, path: string, options?: FORMDATA.AppendOptions|string){
    return [name, createReadStream(path), options]
}

export class FormDataBuilder extends Builder {

    public constructor(...form_datas: [string, ReadStream, FORMDATA.AppendOptions|string][]){
        const form = new FORMDATA()
        for(const [name, stream, options] of form_datas){
            form.append(name, stream, options)
        }

        super({ value: form })
    }

}